<template>
  <div class="echarts"></div>
</template>

<script>
  import echarts from 'echarts'

  export default {
    name: 'MEchart',
    props: {
      option: Object,
      nodes: {
        type: Number,
        default: 60
      }
    },
    watch: {
      'option': {
        handler (n, o) {
          if (this.$EChart && n) this.$EChart.setOption(n)
        },
        deep: true
      }
    },
    methods: {
      update (d, i = 0) {
        let tempd = this.option.series[i].data
        if (Array.isArray(d) && Array.isArray(d[0])) {
          d.forEach((v, i) => {
            if (tempd.length >= this.nodes) tempd.shift()
            tempd.push(v)
          })
        } else {
          if (tempd.length >= this.nodes) tempd.shift()
          tempd.push(d)
        }
      }
    },
    mounted () {
      this.$EChart = echarts.init(this.$el)
    },
    beforeDestroy () {
      this.$EChart.dispose()
    }
  }
</script>

<style lang="scss" src="./style.scss"></style>



